.Puppet{
	position: absolute;
	// left: 40px;
	// bottom: 200px;
	z-index: 10;

	// width: 14px;
	// height: 20px;
	// background: url('../assets/mario.png') no-repeat -209px 0;
	transition: left 40ms linear;

	animation-timing-function: steps(1, start);
	animation-duration: .30s;
	animation-iteration-count: infinite;
}

.left{
	background-position: -169px 0;
}
.right{
	background-position: -209px 0;
}
.walk-left{
	animation-name: walk-left;
}
.walk-right{
	animation-name: walk-right;
}
.lower-left{
	background-position: -90px -37px;
}
.lower-right{
	background-position: -288px -37px;
}
.lookup-left{
	background-position: -49px -39px;
}
.lookup-right{
	background-position: -328px -39px;
}
.jumping-right{
	background-position: -208px -39px;
}
.jumping-left{
	background-position: -168px -39px;
}
.falling-right{
	background-position: -248px -39px;
}
.falling-left{
	background-position: -128px -39px;
}
.dying-right{
	background-position: -368px -38px;
}
.dying-left{
	background-position: -8px -38px;
}

@keyframes walk-left {
	from	{background-position: -169px 0;}
	50%	{background-position: -169px 0;}
	51%	{background-position: -49px 0;}
	to		{background-position: -49px 0;}
}

@keyframes walk-right {
	from	{background-position: -328px 0;}
	50%	{background-position: -328px 0;}
	51%	{background-position: -209px 0;}
	to		{background-position: -209px 0;}
}
